*{
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-family: Arial, sans-serif; 
}
body::-webkit-scrollbar {
  width: 10px;
}
body::-webkit-scrollbar-thumb {
  background: #FE9119;
  border-radius: 50%;
}
/* body::-webkit-scrollbar-thumb:hover {
  background: #555;
} */
body {
  scrollbar-width: thin;
  scrollbar-color: #FE9119 #fff;
}
body::-webkit-scrollbar-thumb {
  background-color: #FE9119;
  border-radius: 6px;
}
body::-webkit-scrollbar-thumb:hover {
  background-color: #fff;
}
body::-webkit-scrollbar-track {
  background-color: #fff;
}


/* Navbar Section CSS Start*/

.logo-image img{
  width: 200px;
  size: fixed;
}
.active{
  color: #FE9119;
  padding: 13px 0px;
  border: none;
  border-bottom: 3px solid #FE9119;
}

.nav-sections>*:hover{
  color: #FE9119;
  padding: 13px 0px;
  border: none;
  border-bottom: 3px solid #FE9119;
}

.nav-sections>*:nth-child(7):hover{
  border: none;
}

.menu:hover{
  border: none;
}
@media all and (max-width:425px) {
  .logo-image img{
    width: 166px;
    size: fixed;
  }
}

/* Navbar Section CSS End*/


/* Home Section CSS Start*/

.hiimage{
  width: 40px;
  size: fixed;
}

@media all and (max-width:479px) {
  .hiimage{
    width: 30px;
  }
}


@media all and (max-width:1055px) {
  #home{
    width: 950px;
  }
}
@media all and (max-width:985px) {
  #home{
    width: 85%;
  }
}

#home>div:nth-child(1){
  width: 47%;
  color: white;
  margin-left: 10px;
}

@media all and (max-width:991px) {
  #home>div:nth-child(1){
    width: 100%;
    margin: auto;
    text-align: center;
  }
}

#home>div:nth-child(2){
  width: 47%;
  display: flex;
  justify-content: flex-end;
}

@media all and (max-width:991px) {
  #home>div:nth-child(2){
    width: 100%;
    margin: auto;
    justify-content: center;
    margin-bottom: 40px;
  }
}
.about-contact>*{
  width: 40px;
  height: 40px;
}
.about-contact>*:hover{
  width: 45px;
  height: 45px;
  transition: height 0.5s, width 0.5s;
}

#resume-button-2{
  width: 150px;
  height: 40px;
  text-align: center;
  align-items: center;
}

#resume-button-2:hover{
  width: 155px;
  height: 45px;
  transition: height 0.5s, width 0.5s;
}



#move-text{
  color: #FE9119;
}

.typed-cursor {
  color: #FE9119;
}

.home-img{
  width: 400px;
  height: 400px;
  size: fixed;
  border-radius: 50%;
  transition: transform 0.3s;
}

@media all and (max-width:991px) {
  .home-img{
    width: 300px;
    height: auto;
    size: fixed;
  }
}
@media all and (max-width:422px) {
  .home-img{
    width: 200px;
    height: auto;
    size: fixed;
  }
}

.home-img:hover {
  animation: moveInAllDirections 1s infinite;
}

@keyframes moveInAllDirections {
  0% {
    transform: translate(0, 0); /* Start with no translation */
  }
  16.6% {
    transform: translate(-5px, 5px); /* Move 5 pixels to the top-left */
  }
  49.8% {
    transform: translate(5px, -5px); /* Move 5 pixels to the bottom-right */
  }
  66.4% {
    transform: translate(-5px, -5px); /* Move 5 pixels to the top-right */
  }
  83% {
    transform: translate(5px, 5px); /* Move 5 pixels to the bottom-left */
  }
  100% {
    transform: translate(0 , 0);
  }
}


/* Home Section CSS End*/

/* About Section CSS Start*/

.skill-box>*{
  text-align: center;
  font-weight: 500;
  box-shadow: 0 19px 38px rgba(0,0,0,.3), 0 15px 12px rgba(0,0,0,.22);
  padding: 30px 0px 15px 0px;
  border-radius: 10px;
  transition: .3s ease-in-out 0s;
}
.skill-box>*:hover{
  cursor: pointer;
  transform: scale(1.1);
}

.project-box,
.git-cards{
  flex-wrap: wrap;
}

.project-box>*,
.git-cards>*{
  transition: all 1s;
}
.contact_cards>*>*{
  transition: all 1s;
  font-weight: bold;
  transition: all 1s;
}
.contact_cards>*>*:hover{
  transition: all 1s;
    -webkit-transform: rotateZ(360deg);
        -ms-transform: rotateZ(360deg);
        transform: rotateZ(360deg);
}
.project-box>*:hover{
    -webkit-transform: rotateZ(3deg);
        -ms-transform: rotateZ(3deg);
        transform: rotateZ(3deg);
}

.git-cards>*:hover{
  transition: all .5s;
  transform: scale(1.1);
}

/* About Section CSS End*/


/* Scroll To Top CSS Start*/

.scroll-to-top {
  position: fixed;
  bottom: 25px;
  right: 15px;
  width: 40px;
  height: 40px;
  background-color: #FE9119;
  box-shadow: rgb(0, 0, 0) 0px 5px 15px;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s, background-color 0.3s;
  z-index: 999;
}

.scroll-to-top.show {
  opacity: 1;
}

.scroll-icon {
  font-size: 20px;
}

/* Scroll To Top CSS End*/



/* Contact Page CSS Start*/


.custom-btn {
  width: 100%;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 8px 20px;
  font-family: 'Lato', sans-serif;
  font-weight: bold;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
   box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  outline: none;
}

.btn-12{
  position: relative;
  right: 20px;
  bottom: 20px;
  border:none;
  box-shadow: none;
  width: 100%;
  height: 40px;
  line-height: 42px;
  -webkit-perspective: 230px;
  perspective: 230px;
}
.btn-12 span {
  background: #FE9119;
background: linear-gradient(0deg, #FE9119 0%, #fc8608 100%);
  display: block;
  position: absolute;
  width: 100%;
  height: 40px;
  box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  border-radius: 5px;
  margin:0;
  text-align: center;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all .3s;
  transition: all .3s;
}
.btn-12 span:nth-child(1) {
  box-shadow:
   -7px -7px 20px 0px #fff9,
   -4px -4px 5px 0px #fff9,
   7px 7px 20px 0px #0002,
   4px 4px 5px 0px #0001;
  -webkit-transform: rotateX(90deg);
  -moz-transform: rotateX(90deg);
  transform: rotateX(90deg);
  -webkit-transform-origin: 50% 50% -20px;
  -moz-transform-origin: 50% 50% -20px;
  transform-origin: 50% 50% -20px;
}
.btn-12 span:nth-child(2) {
  -webkit-transform: rotateX(0deg);
  -moz-transform: rotateX(0deg);
  transform: rotateX(0deg);
  -webkit-transform-origin: 50% 50% -20px;
  -moz-transform-origin: 50% 50% -20px;
  transform-origin: 50% 50% -20px;
}
.btn-12:hover span:nth-child(1) {
  box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
  -webkit-transform: rotateX(0deg);
  -moz-transform: rotateX(0deg);
  transform: rotateX(0deg);
}
.btn-12:hover span:nth-child(2) {
  box-shadow:inset 2px 2px 2px 0px rgba(255,255,255,.5),
   7px 7px 20px 0px rgba(0,0,0,.1),
   4px 4px 5px 0px rgba(0,0,0,.1);
 color: transparent;
  -webkit-transform: rotateX(-90deg);
  -moz-transform: rotateX(-90deg);
  transform: rotateX(-90deg);
}


/* Contact Page CSS End*/